.title {
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    font-size: 48px;
}



.ul {
    display: flex;
    justify-content: center;
    flex-direction: column; 
    list-style-type: none;
}


.li {  
    font-size: 34px;
    color: #000000;
    border: 2px solid #ccc;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    padding: 25px;
    text-align: center;
    margin: 30px;
}


.link {

    text-decoration: none; 
}
